.container {
  width: 65%;
  height: 10%;
  background-color: hsl(216, 12%, 54%);
  border-radius: 25px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 55px;
  color: hsl(25, 97%, 53%);
  font-family: Overpass;
  font-weight: 400;
  font-size: 15px;
}

@media screen and (max-width: 450px) {
  .container {
    font-size: 11px;
  }
}
