.button{
    width: 65px;
    height: 65px;
    background-color: hsl(216, 12%, 54%);
    border-radius: 50%;
    border:  none;
    font-family: Overpass;
    font-weight: 700;
    color: hsl(217, 12%, 63%);
    font-size: 20px;
    transition: all 0.25s ease-in-out;
}

.selectButton{
    width: 65px;
    height: 65px;
    background-color: hsl(25, 97%, 53%);
    border-radius: 50%;
    border:  none;
    font-family: Overpass;
    font-weight: 700;
    color: white;
    font-size: 20px;
    transition: all 0.25s ease-in-out;
}


.button:hover{
    background-color: hsl(217, 12%, 63%);
    color: white;
}